.container_videoPage{
    height: calc(100vh - 3.4rem);
    overflow-y: auto;
    background-color: rgb(11,11,11);
    color: white;
}
.container2_videoPage{
margin: 1.5rem 4.5rem;
display: flex;
}
.video_display_screen_videoPage{
    width: calc(100vw - 4.5rem - 35vw);
}
.video_ShowVideo_videoPage{
    padding: 0.1rem;
    height: 70vh;
    width: 100%;
    background-color: black;
    border: 1px solid rgba(37,37,37,0.2);
}

.videos_btns_title_VideoPage_cont{
    border-bottom: 3px solid rgba(245,222,179,0.175);
    flex-grow: 1;
}
.video_title_VideoPage{
    font-size: x-large  ;
    margin: 0;
    padding: 1rem 0.8rem;
}
.views_date_btns_VideoPage{
    display: flex;
    margin: 0.5rem 1rem;
    width: calc(100vw - 6.5rem - 35vw);
}
.views_VideoPage{
    display: flex;
    font-size: 0.8rem;
    flex-grow: 1;
    align-items: center;
}
.chanel_details_videopage{
    border-bottom: 3px solid rgba(245,222,179,0.175);
    display: flex;
    text-decoration: none;
    color: aliceblue;
    font-weight: bolder;
}
.chanel_logo_videopage{
    display: flex;
    border: 1px solid rgb(0,0,0,0.19);
    width: 4rem;
    height: 4rem;
    margin: 0.5rem;
    background-color: white;
    border-radius: 100%;
}
.chanel_logo_videopage p{
    color: black;
    margin: auto;
    font-size: xx-large;
}
.chanel_name_videopage{
    font-weight: bolder;
    margin: auto 1rem;
    font-size: larger;

}
.comments_videopage{
    width: 100%;
    padding-inline-start: 0.5rem;
}
.moreVideoBar{
    width: calc(100vw - 4.5rem - 70rem);
    padding: 0 1rem;
    
}