.Container_Navbar{
    border: 1px solid black;
    display: flex;
    height: calc(3.5rem-2px);
    background-color:#2e2e2e;
    justify-content:space-between;
  }
  .Burger_Logo_Navbar{
    display: flex;
    height:3.3rem;
    
  }
  .burger{
    display:flex;
    flex-direction:column;
    cursor:pointer;
    justify-content:space-evenly;
    margin: 0.2rem;
    padding: 0.5rem;
    border:1px solid transparent;
    margin-left:0.5rem;
    margin-right:0.7rem;
    border-radius:100%;
  }
  .burger p{
     margin: 0.5px;
    width:1.2rem;
    height:0.1rem;
    background-color: #fff5f5;
  }
  .logo_div_Navbar{
    display:flex;
    text-decoration: none;
  }
  .logo_div_Navbar img{
    margin:auto;
    height:40px;
    width:40px;
  }
  .logo_title_Navbar{
    margin: auto;
    color: white;
    font-weight: bold;  
  }
  
  .apps_Box{
    width:25px;
    margin:auto 0.7rem;
    height:25px;
    display:flex;
    flex-wrap:wrap;
    justify-content:space-around;
  }
  .appbox{
    border:1px solid white;
    margin:0.1rem;
    width:2px;
    height:2px; 
  }
  
  .Auth_cont_Navbar{
    width:fit-content;
    margin:auto 0.5rem;
    display:flex;
    position:relative;
  }
  .Auth_Btn{
    cursor:pointer;
    color:#3ea6ff;
    background-color:rgb(36,36,36);
    border:2px solid;
    padding:0.5rem;
    margin:0;
    border-radius:0.1rem;
  }
  